*
  padding: 0
  margin: 0 auto
  box-sizing: border-box
  text-decoration: none

body
  margin: 0 auto
  min-width:  1750px
  font-family: Forum, Arial, sans-serif
  font-weight: normal
  font-size: 1.1em
  line-height: 27px 
  color: white
  background-color: #3d181d
  background-image: url(../img/imgHeader.jpg)
  background-position: top center
  background-repeat: no-repeat

.visually-hidden 
  position: absolute
  clip: rect(1px, 1px, 1px, 1px)
  padding: 0
  border: 0
  height: 1px
  width: 1px
  overflow: hidden


.container
  width: 1730px
  margin: 0 auto
  padding-left: 10px
  padding-right: 10px



.main-header
  padding: 30px 0

.main-header-nav
  display: flex
  align-items: center

.site-navigation
  padding: 0
  margin: 0
  list-style: none
  margin-left: 290px
  display: flex
  flex-wrap: wrap
  align-items: flex-start
  max-width: 950px

.site-navigation-item
  margin-right: 60px

.site-navigation-item:last-child
  margin-right: 0

.site-navigation-link, .phone
  font-size: 1.2empx
  line-height: 22px
  font-weight: normal
  letter-spacing: .06em
  color: inherit
  text-transform: uppercase

.phone
  margin-left: auto

.title
  width: 800px
  margin: 95px auto 80 auto
  font-weight: normal
  font-size: 120px
  line-height: 132px
  text-align: center
  letter-spacing: 0.09em
  text-transform: uppercase

.promo-button
  margin-bottom: 30px
  align-self: center

.button
  display: inline-block
  padding: 18px 30px 
  color: inherit
  border: 1px solid #fff
  background-color: rgba(0, 0, 0, .3)
  font-weight: normal
  font-size: 12px 
  line-height: 13px 
  text-align: center
  letter-spacing: .15em
  text-transform: uppercase
  transition: .3s

.button:hover
  background-color: white
  transition: .5s
  color: black

.promo
  display: flex
  flex-direction: column

.promo-navigation-wrapper
  width: 100%
  display: flex
  justify-content: space-between
  padding-bottom: 3em

.social-list
  width: 90%
  padding: 0
  margin: 0
  list-style: none

.social-item
  padding: 5px 0

.social-item-link
  font-weight: normal
  font-size: 1.1em
  line-height: 22px
  letter-spacing: .06em
  color: inherit

.promo-link
  font-weight: normal
  font-size: 20px
  line-height: 22px
  letter-spacing: .06em
  color: inherit
  text-transform: lowercase
  transform-origin: 0 0
  transform: translateX(100%) rotate(90deg)

.blur
  background-image: url(../img/blur.png)
  width: 100%
  height: 7.8vh
  background-position: top center
  background-repeat: no-repeat


.catalog
  padding-top: 30px
  padding-bottom: 100px
  background-color: #3b181b

.section-title
  font-weight: normal
  font-size: 30px 
  line-height: 33px 
  text-align: center
  letter-spacing: .06em
  text-transform: uppercase
  padding-bottom: 10px

hr
  height: 3px
  background: white

.catalog-title-shadow
  margin: 0
  font-weight: normal
  font-size: 220px 
  line-height: 220px 
  color: rgba(255, 255, 255, .05)
  text-align: center
  letter-spacing: .06em
  text-transform: uppercase

.catalog-list
  display: flex
  justify-content: flex-start
  align-content: space-between
  align-items: flex-start
  flex-wrap: wrap
  flex-direction: column
  height: 1150px
  padding: 0 70px 0 110px 
  margin: -100px auto 
  list-style: none

.catalog-item 
  display: flex
  flex-direction: column
  width: 330px
  padding: 10px 10px 20px 10px
  border: 1px solid rgba(255, 255, 255, .2)
  margin-right: 40px
  margin-bottom: 40px 

.catalog-item:nth-of-type(4n+1)
  order: 1

.catalog-item:nth-of-type(4n+2)
  order: 2

.catalog-item:nth-of-type(4n+3)
  order: 3

.catalog-item:nth-of-type(4n)
  order: 4

.break
  flex-basis: 100%
  width: 0
  height: 0
  padding: 0
  border: none

.catalog-item 
  img
    order: -1
    width: 300px

.item-num-line
  display: flex
  position: absolute
  margin: 27px 0 0 30px

  .symbol
    font-size: 1.4em
    padding-right: .3em
  
  .line
    width: 70px
    border-bottom: 1px solid white

.catalog-item-title
  margin-left: 5px
  margin-top: 20px
  margin-bottom: 5px
  min-height: 52px
  font-weight: normal
  font-size: 16px 
  line-height: 27px
  letter-spacing: .1em
  text-transform: uppercase
  color: rgba(255, 255, 255, .9)

.catalog-item-link
  text-align: center
  font-size: 12px 
  line-height: 13px
  letter-spacing: .15em
  text-transform: uppercase
  color: white
  border: 1px solid rgba(255, 255, 255, .09)
  width: 300px
  padding: .5em


.sales
  display: flex
  padding-bottom: 100px
  width: 1176px
  margin: 0 auto

  .button
    margin-left: 0 

.sales-text
  display: flex
  flex-direction: column
  justify-content: space-between

.sales-text-column
  padding: 40px 4px 20px 

.sales-img-column
  width: 700px

.sales-text 
  padding: auto
  margin: 0
  line-height: 31px

.section-title-big 
  font-size: 50px
  line-height: 55px
  letter-spacing: .09em
  text-align: left


.portfolio 
  display: flex
  padding-bottom: 245px

.portfolio-border-img
  position: absolute
  margin: 3% 0 0 3%
  width: 996px
  height: 755px
  border: 1px solid rgba(255, 255, 255, .3)

.portfolio-img-column
  width: 996px

.portfolio-text-column 
  padding: 120px 5px 20px 115px

.portfolio-text
  margin: 55px 0 44px 0

.photo-preview-list
  margin-left: -10px
  width: 555px
  list-style-type: none
  display: flex
  flex-direction: space-between
  padding: 30px 0


.certificates
  padding-bottom: 100px

  .button
    margin: 30px 0 0 0

.certificates-list
  padding: 0
  margin: -120px 0 0 0
  list-style: none
  display: flex
  justify-content: center
  align-items: center
  flex-wrap: wrap

.certificates-item 
  display: flex
  flex-direction: column
  align-items: center
  width: 348px
  padding-bottom: 50px
  margin-left: 15px
  margin-right: 15px
  background-color: #281014
  border-radius: 10px
  box-shadow: 0 -10px 30px rgba(0,0,0,.3)

  .logo-card
    position: absolute
    margin-top: 60px

.block-first
  width: 100%
  display: flex
  justify-content: space-between
  align-items: flex-start
  padding: 0

.block-second
  width: 100%
  display: flex
  justify-content: space-between
  align-items: flex-end
  padding: 0

.card-angle
  margin: 0

.certificates-item-big 
  width: 420px
  padding-bottom: 70px

  .button
    margin: 70px 0 0 0
  
.certificate-top 
  width: 100%
  display: flex
  flex-direction: column
  align-items: center
  align-self: stretch
  padding: 20px
  background-color: #0a0a0e
  border-radius: 10px 10px 0 0
  box-shadow: 0 -10px 30px rgba(0,0,0,.3)

.certificate-logo 
  margin-top: 45px
  margin-bottom: 45px

.certificates-item-big .certificate-logo 
  margin-top: 65px
  margin-bottom: 60px

.certificate-number 
  align-self: flex-start
  margin: 0
  font-size: 21px
  line-height: 23px
  letter-spacing: 0.06em

.certificate-price 
  align-self: flex-end
  margin: 0
  font-size: 21px
  line-height: 23px
  letter-spacing: 0.06em

.certificate-text, .certificates-item-big
  text-align: center

.certificate-text
  font-size: 20px
  padding: 60px 40px 30px 40px

.cardUsor
  margin: 0 auto
  padding: 5%


.partners-list 
  display: flex
  flex-wrap: wrap
  list-style: none
  width: 1270px
  margin: 0 auto
  padding: 90px 0 0 45px

.partners-item 
  margin-right: 33px
  margin-bottom: 35px

.partners-item 
  flex-grow: 1
  margin-right: 33px
  margin-bottom: 35px

.partners-item-img 
  width: 100%
  object-fit: cover
  vertical-align: middle


.main-footer 
  padding-top: 15px
  padding-bottom: 60px
  border-top: 1px solid rgba(255, 255, 255, 0.2)

.main-footer-wrapper 
  display: flex
  flex-direction: column
  align-items: center

.main-footer-nav 
  display: flex
  flex-wrap: wrap
  max-width: 1150px
  padding: 0
  margin: 0 0 15px 0
  list-style: none

.main-footer-nav-item 
  margin-right: 60px

.main-footer-nav-item:last-child 
  margin-right: 0

.main-footer-nav-link 
  font-weight: normal
  font-size: 16px
  line-height: 18px
  text-decoration: none
  color: rgba(255, 255, 255, 0.5)

.main-footer-social-list 
  display: flex
  flex-wrap: wrap
  padding: 0
  margin: 0
  list-style: none
  max-width: 700px

.main-footer-social-item 
  margin-right: 50px

.main-footer-social-item:last-child 
  margin-right: 0
